.slider h3 {
  @include fonts(16px, 20px);
  max-width: 230px;
  text-align: center;
  margin: 0 auto;
  padding: 25px 0 15px;
}

.slider p {
  @include fonts(14px, 18px);
  font-family: "poppins-bold", "Arial", sans-serif;
  max-width: 230px;
  text-align: center;
  margin: 0 auto;
  padding: 0 0 60px;
}

